import React, { useState } from "react";
import "./Header.css";
import { Link } from "react-router-dom";

function Header(props) {
  // const [loaded, setLoaded] = useState(false);

  return (
    <header className={"header" + " " + props.className}>
      <div className="language_tag">
        <a
          style={
            props.language.match("en")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
          onClick={() => {
            props.setLanguage("en");
          }}
        >
          EN
        </a>
        &nbsp;&nbsp;|&nbsp;&nbsp;
        <a
          style={
            props.language.match("de")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
          onClick={() => {
            props.setLanguage("de");
          }}
        >
          DE
        </a>
        &nbsp;&nbsp;|&nbsp;&nbsp;
        <a
          style={
            props.language.match("es")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
          onClick={() => {
            props.setLanguage("es");
          }}
        >
          ES
        </a>
      </div>
      {/* <div>
        {!loaded ? (
          <img
            src="./images/dave-hoefler-wINEMQBBOGE-unsplash_low_res.jpg"
            alt="background_image_low_res"
            className="background_image"
          />
        ) : null}
      </div>
      <img
        className="background_image"
        src="./images/dave-hoefler-wINEMQBBOGE-unsplash.jpg"
        alt="background_image"
        style={!loaded ? { visibility: "hidden" } : {}}
        onLoad={() => setLoaded(true)}
      /> */}
      <img
        src="./images/Lisandro_resbaja_web_Seeber_DSC4065-2.jpg"
        alt="personal_image"
        className="image"
      />
      <div className="background_drawings">
        <div className="drawing_1">
          <img
            src="./images/background_drawings/Data Mining.svg"
            alt="drawing_1"
          />
        </div>
        <div className="drawing_2">
          <img
            src="./images/background_drawings/Decision Tree.svg"
            alt="drawing_2"
          />
        </div>
        <div className="drawing_3">
          <img
            src="./images/background_drawings/Data Science.svg"
            alt="drawing_3"
          />
        </div>
        <div className="drawing_4">
          <img
            src="./images/background_drawings/Machine Learning.svg"
            alt="drawing_4"
          />
        </div>
        <div className="drawing_5">
          <img
            src="./images/background_drawings/Data Visualization.svg"
            alt="drawing_5"
          />
        </div>
        <div className="drawing_6">
          <img
            src="./images/background_drawings/Deep Learning.svg"
            alt="drawing_6"
          />
        </div>
        <div className="drawing_7">
          <img
            src="./images/background_drawings/Database.svg"
            alt="drawing_7"
          />
        </div>
        <div className="drawing_8">
          <img
            src="./images/background_drawings/Linear Regression.svg"
            alt="drawing_8"
          />
        </div>
        <div className="drawing_9">
          <img
            src="./images/background_drawings/Machine Learning.svg"
            alt="drawing_9"
          />
        </div>
        <div className="drawing_10">
          <img
            src="./images/background_drawings/Data Visualization.svg"
            alt="drawing_10"
          />
        </div>
        <div className="drawing_11">
          <img
            src="./images/background_drawings/Decision Tree.svg"
            alt="drawing_11"
          />
        </div>
        <div className="drawing_12">
          <img
            src="./images/background_drawings/Database.svg"
            alt="drawing_12"
          />
        </div>
      </div>

      <h1 className="presentation_text">
        <strong>{props.displayText.title}</strong>
        <br />
        {props.displayText.firstPartSubtitle}
        <br />
        {props.displayText.secondPartSubtitle}
        <br />
        {props.displayText.thirdPartSubtitle}
        <br />
      </h1>

      <ul className="links-list">
        <li
          style={
            props.page.match("projects")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
        >
          <Link exact="true" to="/projects">
            {props.displayText.firstPage}
          </Link>
        </li>
        <li
          style={
            props.page.match("awards")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
        >
          <Link exact="true" to="/awards">
            {props.displayText.secondPage}
          </Link>
        </li>
        <li
          style={
            props.page.match("skills")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
        >
          <Link exact="true" to="/skills">
            {props.displayText.thirdPage}
          </Link>
        </li>
      </ul>
    </header>
  );
}

export default Header;
