import React, { useState, useEffect } from "react";
import { Col, Row, Container } from "react-bootstrap";
import "./Certificates.css";

function Certificates(props) {
  useEffect((_) => {
    props.setPage("certificates");
  }, []);

  return (
    <section className={"certificates" + " " + props.className}>
      <Container className="certificates-container">
        <Row xs={1} sm={1} md={1} lg={1} xl={1} className="certificates-row">
          <Col className="certificates-column">
            <section className="certificates-groupset">
              <h1>M.Sc. Data Science</h1>
              <ul>
                <li>Transcript of Records</li>
              </ul>
            </section>
          </Col>
          <Col className="certificates-column">
            <section className="certificates-groupset">
              <h1>Dipl.-Ing. Univ. in Electronics</h1>
              <ul>
                <li>Transcript of Records</li>
              </ul>
            </section>
          </Col>
          <Col className="certificates-column">
            <section className="certificates-groupset">
              <h1>English Certificates</h1>
              <ul>
                <li>TOEFL iBT (11/2020)</li>
                <li>Cambrigde FCE B2 (12/2013)</li>
              </ul>
            </section>
          </Col>
          <Col className="certificates-column">
            <section className="certificates-groupset">
              <h1>German Certificates and Courses</h1>
              <ul>
                <li>Universität Trier - Sprechkompetenz B2 course (08/2021)</li>
                <li>Deutsch Akademie - Intensive B2 course (04/2021)</li>
                <li>Goethe Zertifikat B1 (08/2019)</li>
              </ul>
            </section>
          </Col>
          <Col className="certificates-column">
            <section className="certificates-groupset">
              <h1>Awards</h1>
              <ul>
                <li>1º Place - AADECA 2020 (10/2020)</li>
                <li>2º Place - JAIIO 2020 (10/2020)</li>
                <li>1º Place - SASE 2019 (07/2019)</li>
                <li>1º Place - JONICA 2018 (10/2018)</li>
              </ul>
            </section>
          </Col>
          <Col className="certificates-column">
            <section className="certificates-groupset">
              <h1>Courses</h1>
              <ul>
                <li>
                  The Complete SQL Bootcamp 2020: Go from Zero to Hero - Udemy
                  (11/2020)
                </li>
                <li>Machine Learning – Coursera (10/2020)</li>
                <li>
                  Building a REST API Using Python and Flask - Pluralsight
                  (10/2019)
                </li>
                <li>How Git Works - Pluralsight (09/2019)</li>
                <li>
                  Python for Data Science and Machine Learning Bootcamp - Udemy
                  (03/2019)
                </li>
              </ul>
            </section>
          </Col>
          <Col className="certificates-column">
            <section className="certificates-groupset">
              <h1>Proof of work</h1>
              <ul>
                <li>PricewaterhouseCoopers (PwC) (10/2020)</li>
              </ul>
            </section>
          </Col>
          <Col className="certificates-column">
            <section className="certificates-groupset">
              <h1>Recommendation Letters</h1>
              <ul>
                <li>from PwC AR Manager (10/2020)</li>
                <li>from PwC US Manager (10/2020)</li>
                <li>from Dipl.-Ing. Final Project Director (11/2020)</li>
              </ul>
            </section>
          </Col>
        </Row>
      </Container>
    </section>
  );
}

export default Certificates;
