import React, { useState } from "react";
import {
  HashRouter as Router,
  // BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import "./App.css";
import Header from "./components/Header";
import Projects from "./components/Projects";
import Awards from "./components/Awards";
import Skills from "./components/Skills";
import Footer from "./components/Footer";
import Certificates from "./components/Certificates";
import displayText from "./DisplayText";

function App(props) {
  const [page, setPage] = useState("projects");
  const [language, setLanguage] = useState("en");

  return (
    <div className="App">
      <Router>
        <Header
          className="App-header"
          page={page}
          setLanguage={setLanguage}
          language={language}
          displayText={displayText[language]["header"]}
        />
        {/* <section> */}
        <Switch>
          <Route exact path="/">
            <Redirect to="/projects" />
          </Route>
          <Route
            path="/projects"
            exact
            render={() => {
              return (
                <Projects
                  className="App-body"
                  displayText={displayText[language]["projects"]}
                  setPage={setPage}
                />
              );
            }}
          />
          <Route
            path="/awards"
            exact
            render={() => {
              return (
                <Awards
                  className="App-body"
                  displayText={displayText[language]["awards"]}
                  setPage={setPage}
                />
              );
            }}
          />
          <Route
            path="/skills"
            exact
            render={() => {
              return (
                <Skills
                  className="App-body"
                  displayText={displayText[language]["skills"]}
                  setPage={setPage}
                />
              );
            }}
          />
          <Route
            path="/certificates"
            exact
            render={() => {
              return (
                <Certificates
                  className="App-body"
                  // displayText={displayText[language]["certificates"]}
                  setPage={setPage}
                />
              );
            }}
          />
          <Route>
            <Redirect to="/projects" />
          </Route>
        </Switch>
        {/* </section> */}
        <Footer
          className="App-footer"
          displayText={displayText[language]["footer"]}
        />
      </Router>
    </div>
  );
}

export default App;
